.crear-tarea {
    max-width: 80rem;
    margin: 2rem auto;
    background-color: #fff;
    padding: 2rem;
    border-radius: 1rem;
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.05);
    border: 1px solid var(--border);

    h2 {
        text-align: center;
        margin-bottom: 2rem;
        color: var(--text);
        font-size: 2.4rem;
    }

    .tarea-form {
        display: flex;
        flex-direction: column;
        gap: 1.5rem;
        margin-top: 1rem;

        .form-group {
            display: flex;
            flex-direction: column;

            label {
                margin-bottom: 1rem;
                font-weight: 600;
                color: var(--text);
                font-size: 1.5rem;
            }

            input,
            textarea {
                padding: 0.75rem 1rem;
                border: 1px solid var(--border);
                border-radius: 0.5rem;
                color: var(--text);
                background-color: #f9fafb;

                &:focus {
                    outline: none;
                    border-color: var(--primary);
                    background-color: #fff;
                }
            }
        }
    }
}

// Responsivo
@media (max-width: 600px) {
    .crear-tarea {
        padding: 1.5rem;
        margin: 1rem;

        h2 {
            font-size: 1.5rem;
        }

        .tarea-form {
            .btn-crear {
                width: 100%;
            }
        }
    }
}