:root {
    --bg: #f9fafb;
    --sidebar-bg: #fff;
    --primary: #2563eb;
    --text: #111827;
    --gray: #6b7280;
    --border: #e5e7eb;
}

.sidebar {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    background: var(--sidebar-bg);
    width: 220px;
    padding: 1rem;
    border-right: 1px solid var(--border);
    box-shadow: 2px 0 6px rgba(0, 0, 0, 0.05);
    position: fixed;
    height: 100%;

    .nav-container {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;

        .logo {
            width: 15rem;
        }

        .nav-links a {
            display: block;
            margin-bottom: 1rem;
            text-decoration: none;
            color: var(--gray);
            font-size: 2rem;
            font-weight: 500;

            &:hover {
                color: var(--primary);
            }
        }
    }
}


@media (max-width: 768px) {
    .dashboard {
        .sidebar {
            transform: translateX(-100%);
            position: absolute;
            z-index: 10;
        }
    }
}